<template>
  <!-- 高级参照管理 -->
  <div data-app='true'>
    <div class="parentPage"
         v-if="$route.name === 'referToList'">
      <mrc_ess_table :headers='headers'
                     pageName='add-reference'
                     :paginationOptions='params'
                     @turnPage='getData'
                     :isLoading='loading'
                     :items='desserts'>
        <template v-slot:item.actions="{ item }">
          <menu-list :list='list'
                     @handleClick='handleClick($event,item)'>
            <template v-slot:showAction>
              <v-icon>mdi-dots-vertical</v-icon>
            </template>
          </menu-list>

        </template>
      </mrc_ess_table>
    </div>
    <!-- 这是当前页的子页面  嵌套路由 -->
    <transition name="fade-transform"
                mode="out-in"
                v-else>
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import { getList, getDetele } from '@/service/api/prompt'
import confirm from '@/components/confirm'
export default {
  components: { confirm },
  props: {},
  data () {
    return {
      isLoading: false,
      params: {
        '~table~': 'mrbase_sys_prompt',
        cpage: this.$store.state.cpage,
        pagesize: this.$store.state.pageSize,
        totalCount: 0, //总条数
        totalpage: 0, //总页数
      },
      desserts: [],
      loading: false

    };
  },
  created () { },
  mounted () {
    this.getData()
  },
  methods: {
    getData (obj) {
      if (obj) {
        this.params = obj
      }
      this.loading = true
      getList({ cpage: this.params.cpage, pagesize: this.params.pagesize, '~table~': this.params['~table~'] })
        .then(res => {
          this.loading = false
          this.showSkeleton = false
          this.desserts = [...res.data]
          // this.totalpage = res.totalpage
          this.params.totalCount = res.totalCount
          this.params.totalpage = Math.ceil(res.totalCount / res.pagesize)
          // this.totalpage = Math.ceil(res.totalCount / res.pagesize)
        })
        .catch(err => {
          console.log(err)
        })
    },
    handleClick ({ index }, item) {
      if (index == 0) {
        this.$store.commit('set_subPage_name', item.prompt_name)
        this.$store.commit('set_uuid', item.uuid)
        this.$store.commit('set_iconstatus', true)
        this.$router.push({
          name: 'detail-reference',
          params: {
            name: item.prompt_name,
            uuid: item.uuid
          }
        })
      }
      // 编辑
      if (index == 1) {
        this.editPrompt(item)
      }
      if (index == 2) {
        this.handleDel(item)
      }
    },
    // 编辑
    editPrompt (item) {
      this.$store.commit('set_subPage_name', item.prompt_name)
      this.$store.commit('set_uuid', item.uuid)
      this.$store.commit('set_iconstatus', false)
      this.$router.push({
        name: 'detail-reference',
        params: {
          name: item.prompt_name,
          uuid: item.uuid
        }
      })
    },
    // 点击删除
    handleDel (item) {
      this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') })
        .then(() => {
          const obj = Object.assign({}, { uuid: item.uuid, '~table~': 'mrbase_sys_prompt' })
          getDetele(obj)
            .then(res => {
              this.isOpen = false
              this.isLoading = false
              this.$Message({ text: this.$t('info.del_success') })
              // this.desserts.length === 1  只有一条数据 删除就请求上一页的数据
              if (this.desserts.length === 1 && this.params.cpage != 1) {
                this.params.cpage--
                this.params.totalpage--
              }
              this.getData()
            })
            .catch(err => {
              console.log(err)
            })
        }).catch(() => { })
    },
    // 添加参照
    add_prompt () {
      this.$store.commit('set_subPage_name', '')
      this.$store.commit('set_uuid', '')
      this.$router.push({
        name: 'add_edit_advance'
      })
    }
  },
  computed: {
    list () {
      return [
        {
          name: this.$t('label_View')
        },
        {
          name: this.$t('label_Edit')
        },
        {
          name: this.$t('label_delete')
        }
      ]
    },
    headers () {
      const headers = [
        {
          text: this.$t('label_With_reference_to_the_name'),
          align: 'start',
          sortable: false,
          value: 'prompt_name',
        },
        {
          text: this.$t('label_type'),
          align: 'start',
          sortable: false,
          width: '100px',
          value: 'prompt_type',
        },
        {
          text: this.$t('label_The_source_table_name'),
          align: 'start',
          sortable: false,
          value: 'source_table',
        },
        {
          text: this.$t('label_Take_the_column_value'),
          align: 'start',
          sortable: false,
          value: 'value_field',
        },
        {
          text: this.$t('label_Show_columns'),
          align: 'start',
          sortable: false,
          value: 'view_fields',
        },
        {
          text: '',
          align: 'start',
          sortable: false,
          value: 'actions',
        },
      ]
      return headers
    }
  },
  watch: {
    '$route' (val) {
      if (val.name === 'referToList') {
        this.showSkeleton = true
        this.getData()
      }
    }
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  }
};
</script>

<style scoped>
.btnGroup {
  position: relative;
}
.addBtn {
  color: #fff;
  position: absolute;
  top: -55px;
  right: 10px;
  transform: rotateZ(45deg);
}
</style>
